<template lang="">
    <div class="inventoryAr">
        <div class="container">
            <div class="inventoryAr-title">
                <h2>{{inventoryCar.titleCar}}</h2>
                <p>Watching now 8 people</p>
            </div>
            <div class="inventoryAr-main">
                <div class="inventoryAr-main_left" v-if="interior === false">
                    <swiper   
                        @swiper="setThumbsSwiper"
                        :freeMode="true"
                        :watchSlidesProgress="true"
                        :loop="true"
                        :modules="modules"
                        class="mySwiper9"
                    >
                        <swiper-slide class="inventoryAr-slide"
                        ><img
                            :src='inventoryCar.exterior.image' /></swiper-slide
                        ><swiper-slide class="inventoryAr-slide"
                        ><img
                        :src='inventoryCar.exterior.image2' /></swiper-slide
                        ><swiper-slide class="inventoryAr-slide"
                        ><img
                        :src='inventoryCar.exterior.image3' />
                        </swiper-slide>
                    </swiper>
                    <swiper
                        :spaceBetween="10"
                        :slidesPerView="3"
                        :navigation="true"
                        :thumbs="{ swiper: thumbsSwiper }"
                        :loop="true"
                        :modules="modules"
                        class="mySwiper10"
                    >
                    <swiper-slide class="inventoryAr-slide2"
                        ><img
                            :src='inventoryCar.exterior.image' /></swiper-slide
                        ><swiper-slide class="inventoryAr-slide2"
                        ><img
                        :src='inventoryCar.exterior.image2' /></swiper-slide
                        ><swiper-slide class="inventoryAr-slide2"
                        ><img
                        :src='inventoryCar.exterior.image3' />
                        </swiper-slide>
                    </swiper>
                <router-link class="inventoryAr-btn2" to="">Request more information</router-link>
                </div>
                    <div class="inventoryAr-main_left" v-if="interior === true">
                    <swiper   
                        @swiper="setThumbsSwiper"
                        :freeMode="true"
                        :watchSlidesProgress="true"
                        :loop="true"
                        :modules="modules"
                        class="mySwiper9"
                    >
                        <swiper-slide class="inventoryAr-slide"
                        ><img
                            :src='inventoryCar.interior.image4' /></swiper-slide
                        ><swiper-slide class="inventoryAr-slide"
                        ><img
                        :src='inventoryCar.interior.image5' /></swiper-slide
                        ><swiper-slide class="inventoryAr-slide"
                        ><img
                        :src='inventoryCar.interior.image6' />
                        </swiper-slide>
                    </swiper>
                    <swiper
                        :spaceBetween="10"
                        :slidesPerView="3"
                        :navigation="true"
                        :thumbs="{ swiper: thumbsSwiper }"
                        :loop="true"
                        :modules="modules"
                        class="mySwiper10"
                    >
                    <swiper-slide class="inventoryAr-slide2"
                        ><img
                            :src='inventoryCar.interior.image4' /></swiper-slide
                        ><swiper-slide class="inventoryAr-slide2"
                        ><img
                        :src='inventoryCar.interior.image5' /></swiper-slide
                        ><swiper-slide class="inventoryAr-slide2"
                        ><img
                        :src='inventoryCar.interior.image6' />
                        </swiper-slide>
                    </swiper>
                <router-link class="inventoryAr-btn2" to="">Request more information</router-link>
                </div>    
                <div class="bottom-btn">
                    <div>
                       <button class="bottom-button" :class="{active: interior === false}"
                        @click="interior = !interior">Exterior</button>
                        <button class="bottom-button" :class="{active: interior === true}"
                        @click="interior = !interior">Interior</button> 
                    </div>
                    <img src="../assets/img/inventory/Share.png" alt="">
                </div>               
                
                <div class="inventoryAr-main_right">
                    <div class="har-top">
                        <span class="span-title">Price</span>
                        <p>{{inventoryCar.price}} $</p>
                        <div class="har-top_display">
                            <div class="har-top_main">
                            <div>
                                <span class="span-title">Body</span>
                                <p>{{inventoryCar.body}}</p> 
                            </div>
                            <div>
                                <span class="span-title">Transmission</span>
                                <p>{{inventoryCar.transmission}}</p> 
                            </div>
                        </div>
                        <div class="har-top_main">
                            <div>
                                <span class="span-title">Year</span>
                                <p>{{inventoryCar.year}}</p> 
                            </div>
                            <div>
                                <span class="span-title">Kilometres </span>
                                <p>{{inventoryCar.kilometres }}</p> 
                            </div>
                        </div>
                        </div>
                    </div>
                    <span class="span-tr"></span>
                    <span class="span-title">Detail</span>
                    <div class="har-center">
                        <div class="har-center_main">
                            <div>
                                <p>Rear wheel drive</p>
                                <p>6 cylinders</p>
                            </div>
                            <div>
                                <p>11 l. per 100 km.</p>
                                <p>Rear wheel drive</p>
                            </div>
                        </div>
                        <div class="har-center_main">
                            <div>
                                <p>2500-4000 rpm</p>
                                <p>0-100 in 2.7 s.</p>
                            </div>
                            <div>
                                <p>Power steering</p>
                                <p>2500-4000 rpm</p>
                            </div>
                        </div>
                    </div>
                    <span class="span-tr"></span>
                    <span class="span-title">Description</span>
                    <div class="har-bottom">
                        <div>
                            <span class="span-title">Porsche 911 - a sports car manufactured by the German company Porsche AG in the back of a two-door coupe or a convertible based on it, produced in different generations from 1964 to the present day. 
                            <br/>The 911 index was not originally planned for the timeless designation of different generations of the same car, and was no more than one of many similar in the through three-digit internal factory numbering of all Porsche models assigned to a very specific 1964 model.
                            </span>
                        </div>
                    </div>
                    <router-link class="inventoryAr-btn" to="">Request more information</router-link>
                </div>
            </div>
        </div>
    </div>
    <CalculatorInput class="letsfigure"/>
</template>
<script>

import {mapGetters} from 'vuex'
import CalculatorInput from '@/components/CalculatorInput.vue';

// Import Swiper Vue.js components
import { Swiper, SwiperSlide } from 'swiper/vue';

// Import Swiper styles
import 'swiper/css';

import "swiper/css/free-mode"
import "swiper/css/navigation"
import "swiper/css/thumbs"

// import required modules
import {FreeMode,Navigation,Thumbs} from 'swiper';

export default {
    
    data(){
        return{
            interior: false
        }
    },

    computed:{
        ...mapGetters([
            'inventoryCars'
        ]),
        inventoryCar(){
            let result = {}
            let vm = this
            this.inventoryCars.map(function(item){
                if(item.id == vm.$route.query.inventory){
                    result = item
                }
            })
            return result
        }
    },

    components: {
        Swiper,
        SwiperSlide,
        CalculatorInput,
    },
    setup() {
        let thumbsSwiper = null;

        const setThumbsSwiper = (swiper) => {
            thumbsSwiper = swiper;
        }

        return {
            modules: [FreeMode,Navigation,Thumbs],
        };
    }
}
</script>
<style lang="scss">

@import '../assets/scss/breakpoints.scss';

.letsfigure{
    margin-bottom: 40px;
    @include breakpoints(sm){
        margin-bottom: 30px;
    }
}

.inventoryAr-main_left{
    width: 48%;
    @include breakpoints(lg){
        width: 100%;
    }
}
.inventoryAr-slide{
    img{
        width: 100%;
        aspect-ratio: 1/.56;
    }
}
.inventoryAr-slide2{
    img{
        width: 100%;
        height: 100%;
        aspect-ratio: 1/.56;
    }
}
.mySwiper9{
    margin-bottom: 15px;
    @include breakpoints(sm){
        margin-bottom: 10px;
    }
    .swiper-wrapper{
        gap: 0px;
    }
}
.mySwiper10{
    .swiper-slide{
        height: auto;
    }
    .swiper-slide-active {
        border: 2px solid blue;
    }
.swiper-button-next{
    background-image: url(../assets/img/home-page/arrow-next2.png);
    background-repeat: no-repeat;
    width: 53px;
    height: 53px;
    background-size: 100% 100%;
    right: 0;
    @include breakpoints(sm){
        width: 35px;
        height: 35px;
    }

    &::after{
        display: none;
    }
}
.swiper-button-prev{
  background-image: url(../assets/img/home-page/arrow-prev2.png);
  background-repeat: no-repeat;
  width: 53px;
  height: 53px;
  background-size: 100% 100%;
  left: 0;
  @include breakpoints(sm){
    width: 35px;
    height: 35px;
  }

  &::after{
    display: none;
  }
}
}
.inventoryAr {
    margin: 40px 0;
    @include breakpoints(lg){
        width: 90%;
        margin: 40px auto 0;
    }
    @include breakpoints(sm){
        width: 100%;
        margin: 20px 0;
    }
}
.inventoryAr-title {
    display: flex;
    flex-direction: column;
    @include breakpoints(sm){
        gap: 10px;
    }

    h2{
        font-family: 'Gilroy700';
        font-size: 45px;
        line-height: 140.5%;
        color: #41456B;
        @include breakpoints(sm){
            font-size: 30px;
        }
    }
    p{
        font-family: 'Gilroy500';
        font-size: 16px;
        line-height: 159%;
        color: #606276;
        @include breakpoints(sm){
            font-size: 14px;
        }
    }
}
.inventoryAr-main {
    display: flex;
    justify-content: space-between;
    margin-top: 20px;
    position: relative;
    @include breakpoints(lg){
        flex-direction: column;
        gap: 150px;
    }
    @include breakpoints(sm){
        gap: 80px;
    }
}
.inventoryAr-main_right {
    width: 48%;
    @include breakpoints(lg){
        width: 80%;
    }
    @include breakpoints(sm){
        width: 100%;
    }
}
.har-top {
    p{
        font-family: 'Gilroy700';
        font-size: 25px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #7481FF;
    }
}
.span-title {
    font-family: 'Gilroy500';
    font-size: 16px;
    line-height: 159%;
    color: #606276;
}
.har-top_display{
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin-top: 20px;
    @include breakpoints(sm){
        width: 100%;
    }
}
.har-top_main {
    display: flex;
    flex-direction: column;
    gap: 20px;

    div{
        display: flex;
        flex-direction: column;
    }

    p{
        font-family: 'Gilroy700';
        font-size: 25px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: #41456B;
    }
}
.span-tr {
    display: block;
    width: 100%;
    height: 1px;
    background: #D7D7D7;
    margin: 60px 0;
    @include breakpoints(sm){
        margin: 40px 0;
    }
}
.har-center {
display: flex;
justify-content: space-between;
width: 90%;
margin-top: 20px;
@include breakpoints(sm){
    width: 100%;
    flex-direction: column;
    gap: 20px;
}

    &_main{
        display: flex;
        flex-direction: column;
        gap: 20px;

        div{
            display: flex;
            flex-direction: column;
            gap: 20px;
        }
        p{
            font-family: 'Gilroy600';
            font-size: 20px;
            line-height: 25px;
            color: #41456B;
        }
    }
}
.har-bottom {
    margin-top: 20px;
}
.inventoryAr-btn, .inventoryAr-btn2 {
    display: block;
    margin-top: 40px;
    background: #7481FF;
    border-radius: 2px;
    width: 280px;
    font-family: 'Gilroy700';
    font-size: 16px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #FFFFFF;
    padding: 15px;
    @include breakpoints(sm){
        width: 247px;
        padding: 10px;
        font-size: 14px;
        margin-top: 30px;
    }
}
.inventoryAr-btn2 {
   display: none;
   @include breakpoints(sm){
    display: block;
    margin-top: 20px;
    width: 100%;
   } 
}



.bottom-btn {
    display: flex;
    justify-content: space-between;
    align-items: center;
    position: absolute;
    margin-top: 23%;
    z-index: 1;
    left: 2%;
    right: 54%;
    @include breakpoints(lg){
        margin-top: 49%;
        left: 3%;
        right: 3%;
    }
    @include breakpoints(md){
        margin-top: 46%;
    }
    @include breakpoints(sm){
        margin-top: 44%;
    }

    div{
        display: flex;
        gap: 7px;
        @include breakpoints(sm){
            gap: 10px;
        }
    }

