        background: #FFFFFF;
        color: #41456B;
    }

    img{
        cursor: pointer;
    }
}
.bottom-button {
    background: rgba(215, 215, 215, 0.2);
    border-radius: 2px;
    width: 110px;
    height: 35px;
    font-family: 'Gilroy700';
    font-size: 16px;
    line-height: 14px;
    text-align: center;
    letter-spacing: 0.02em;
    text-transform: uppercase;
    color: #FFFFFF;
    @include breakpoints(sm){
        width: 101px;
        height: 29px;
    }
}
    
</style>